import { newE2EPage } from '@stencil/core/testing';

describe('presto-vimeo', () => {
  it('renders', async () => {
    const page = await newE2EPage();
    await page.setContent('<presto-vimeo></presto-vimeo>');

    const element = await page.find('presto-vimeo');
    expect(element).toHaveClass('hydrated');
  });
  it('renders private videos', async () => {
    const page = await newE2EPage();
    await page.setContent('<presto-vimeo src="https://vimeo.com/611387144/940d77eca0"></presto-vimeo>');

    const element = await page.find('presto-vimeo .plyr__video-embed');
    expect(element.getAttribute('data-plyr-embed-hash')).toContain('940d77eca0');
  });
});
